.word-div {
  padding: auto;
  margin: auto;
  text-align: center;
  padding-bottom: 40px;
}

.word-card {
  background-image: url(../images/background-crumpled-paper-sheet_1194-7545.jpg);
  color: rgb(43, 41, 41);
  border-radius: 5%;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-style: italic;
  font-weight: bolder;
  font-size: 1.75rem;
}

@media screen and (max-width: 600px) {
  .word-div {
    width: 100%;
  }
  .word-card {
    font-size: 1rem;
  }
}
